/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Helmet } from 'react-helmet';
import type { FC } from 'react';

import { useSiteMetadata } from './hooks/use-site-metadata';
import { useDefaultPostImage } from './hooks/use-default-post-image';

type Lang = 'en' | 'tl';

type Meta = {
  content: string;
  name: string;
};

type SeoProps = {
  /** If set to true will append the site title at the end of the description. */
  appendSiteTitleToDescription?: boolean;
  /** The document title. */
  title?: string;
  /** The URL path. */
  urlSlug?: string;
  /** Description of the document. */
  description?: string;
  /** Keywords for the document.  */
  keywords?: string[];
  /** Language of the document. */
  lang?: Lang;
  /** Additional information for crawlers. */
  meta?: Meta[];
  /** Twitter handle related to the content. */
  twitter?: string;
  /** The author of the contents. */
  author?: string;
  /** A URL for an image to be used when sharing. */
  image?: string;
  /** Another additional information used when sharing. */
  pageType?: 'website' | 'article';
  /** Publised date formatted as an ISO 8601 date. */
  publishedAt?: string;
};

const Seo: FC<SeoProps> = ({
  appendSiteTitleToDescription = false,
  title,
  urlSlug = '',
  description,
  keywords = [],
  lang = 'en',
  meta = [],
  twitter,
  author,
  image,
  pageType = 'website',
  publishedAt,
}) => {
  const defaultPostImage = useDefaultPostImage();
  const siteMetadata = useSiteMetadata();

  const metaDescription = description || siteMetadata.description;
  const pageUrl = `${siteMetadata.siteUrl}/${urlSlug}`;
  const pageImage = siteMetadata.siteUrl + (image || defaultPostImage.fluid.src);
  const twitterHandle = `@${siteMetadata.social.twitter}`;
  const titleTemplate = appendSiteTitleToDescription
    ? `%s  ${metaDescription}  ${siteMetadata.title}`
    : `%s  ${metaDescription}`;

  return (
    <Helmet
      htmlAttributes={{ lang }}
      title={title}
      titleTemplate={titleTemplate}
      meta={[
        {
          content: author || siteMetadata.author,
          name: 'author',
        },
        {
          content: metaDescription,
          name: 'description',
        },
        {
          content: pageImage,
          property: 'og:image',
        },
        {
          content: metaDescription,
          property: 'og:description',
        },
        {
          content: pageUrl,
          property: 'og:url',
        },
        {
          content: lang,
          property: 'og:locale',
        },
        {
          content: siteMetadata.title,
          property: 'og:site_name',
        },
        {
          content: title,
          property: 'og:title',
        },
        {
          content: pageType,
          property: 'og:type',
        },
        {
          content: publishedAt,
          property: 'article:published_time',
        },
        {
          content: keywords[0] || siteMetadata.title,
          property: 'article:tag',
        },
        {
          content: 'summary_large_image',
          name: 'twitter:card',
        },
        {
          content: twitterHandle,
          name: 'twitter:site',
        },
        {
          content: twitter || twitterHandle,
          name: 'twitter:creator',
        },
        {
          content: title,
          name: 'twitter:title',
        },
        {
          content: pageUrl,
          name: 'twitter:url',
        },
        {
          content: pageImage,
          name: 'twitter:image',
        },
        {
          content: metaDescription,
          name: 'twitter:description',
        },
      ]
        .concat(
          keywords.length > 0
            ? {
                content: keywords.join(', '),
                name: 'keywords',
              }
            : [],
        )
        .concat(meta)}
    >
      <html lang={lang} />
    </Helmet>
  );
};

export default Seo;
