import { graphql, useStaticQuery } from 'gatsby';

type Social = {
  github: string;
  linkedin: string;
  twitter: string;
};

type SiteMetadata = {
  author: string;
  description: string;
  siteUrl: string;
  social: Social;
  tagline: string;
  title: string;
};

type Data = {
  site: {
    siteMetadata: SiteMetadata;
  };
};

export const useSiteMetadata = () => {
  const data = useStaticQuery<Data>(graphql`
    {
      site {
        siteMetadata {
          author
          description
          siteUrl
          social {
            github
            linkedin
            twitter
          }
          tagline
          title
        }
      }
    }
  `);

  return data.site.siteMetadata;
};
